<template>
  <div>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>评论管理</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card class="box-card">
      <div class="tool_group">
        <div class="btn_box">
          <router-link :to="{ path: '/superMarket/adAdd' }"><el-button class="inquire1" type="success" icon="el-icon-plus" size="mini">添加广告</el-button></router-link>
          <!-- 后端没有接口<el-button type="danger" icon="el-icon-delete" size="mini">批量删除</el-button> -->
        </div>
        <div class="fs-sm">
          广告名称
          <el-input class="inputttt" size="mini"></el-input>
          <el-button class="inquire" type="success" style="width: 80px" size="mini">查询</el-button>
        </div>
      </div>

      <div class="table_box">
        <el-table :data="AdList" style="width: 100%" height="500">
          <el-table-column type="selection" width="55" align="center"></el-table-column>
          <el-table-column prop="sn" label="排序" width="55" align="center"></el-table-column>
          <el-table-column label="图片" width="250" align="center">
            <template slot-scope="scope">
              <el-image class="cover" :src="scope.row.imageUrl" fit="cover"></el-image>
            </template>
          </el-table-column>
          <el-table-column prop="advertisingUrl" label="链接" align="center" show-overflow-tooltip></el-table-column>
          <el-table-column prop="createTime" label="加入时间" align="center" show-overflow-tooltip></el-table-column>
          <el-table-column label="操作" width="150" align="center">
            <template slot-scope="scope">
              <div class="switch_group">
                <div class="switch_btn">
                  <el-switch v-model="scope.row.enableStatus" active-color="#13ce66" inactive-color="#ff4949" :width="50" @change="onSwitch(scope)"></el-switch>
                  <span v-if="scope.row.enableStatus" class="switch_text_true">启用</span>
                  <span v-else class="switch_text_false">禁用</span>
                </div>
                <router-link class="edit_btn" :to="{ path: '/superMarket/adAdd', query: { id: scope.row.id } }">编辑</router-link>
                <el-button type="text" @click="deleteAd(scope, AdList)"><span class="delete_btn">删除</span></el-button>
              </div>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination background layout="prev, pager, next" :total="AdListTotal" @current-change="handleCurrentChange"></el-pagination>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'SuperMarketAdList',

  data() {
    return {
      adCoverUse: false, // 广告是否启用
      pageNo: 1, // 广告列表分页默认为1第一页
      AdList: [], // 广告列表
      AdListTotal: 0 // 广告分页总条数
    }
  },

  methods: {
    // 删除广告
    deleteAd (scope, rows) {
      this.$confirm('此操作将永久删除该广告, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this._deleteAdItem(scope.row.id)
          .then((res) => {
            if (res.data.success) {
              this.$message.success('删除成功')
              rows.splice(scope.$index, 1)
            } else {
              this.$message.error(res.data.msg)
            }
          })
      })
    },
    // 分页
    handleCurrentChange (val) {
      this.pageNo = val
      this._getAdList()
    },
    // 启用/禁用
    onSwitch (scope) {
      // 是否启用状态 0-未启用 1-已启用
      const enableStatus = scope.row.enableStatus ? 1 : 0
      this.apiEnableOrdisable(scope.row.id, enableStatus)
    },

    // 接口api ------------------------------------------------------
    // 获取广告列表
    _getAdList () {
      const DATA = {
        pageNo: this.pageNo
      }
      this.$http.post('/woho/shop/advertising/query', DATA)
        .then((res) => {
          console.log(res)
          let AdList = res.data.data.list
          AdList.map((item) => {
            // 是否启用状态 0-未启用 1-已启用
            if (item.enableStatus === 0 || item.enableStatus === '') {
              item.enableStatus = false
            } else {
              item.enableStatus = true
            }
          })
          this.AdList = AdList
          this.AdListTotal = res.data.data.total
        })
    },
    // 删除一条广告
    _deleteAdItem (id) {
      return this.$http.delete('/woho/shop/advertising/delete/' + id).then((res) => res)
    },
    // 启用/禁用
    apiEnableOrdisable (id, enableStatus) {
      this.$http.post(`/woho/shop/advertising/enableOrdisable/${id}/${enableStatus}`)
    },
  },

  mounted () {
    this._getAdList()
  }
}
</script>

<style lang="scss" scoped>
  $themColor: #5bd9be;

  .box-card {
    min-width: 1100px;
    margin-top:15px;
    .tool_group{
      width: 100%;
      display: flex;
      justify-content: space-between;
      margin-top: 30px;
      .inputttt{
        width: 200px;
        margin: auto 5px;
      }
    }
    .table_box{
      margin: 48px 0px 0px 9px;
      width: 100%;
      .cover{
        width: 215px;
        height: 99px;
        border-radius: 5px;
      }
      .switch_group{
        display: flex;
        align-items: center;
        .switch_btn{
          margin-right: 10px;
          position: relative;
          color: #fff;
          font-size: 8px;
          .switch_text_true{
            position: absolute;
            top: 1px;
            left: 5px;
            pointer-events: none;
          }
          .switch_text_false{
            position: absolute;
            top: 1px;
            left: 20px;
            pointer-events: none;
          }
        }
        .edit_btn{
          color: $themColor;
          margin-right: 10px;
        }
        .delete_btn{
          color: #dd3737;
        }
      }
    }
  }
</style>
